@use "global" as *;

.post-works {
  margin-top: rem(40);
  padding-bottom: rem(100);
  @include mq('md') {
    margin-top: rem(100);
    padding-bottom: rem(180);
  }
}

.post-works__inner {
  max-width: 500px;
  margin-inline: auto;
  padding-inline: var(--padding-inner);
  @include mq('md') {
    max-width: calc(rem(1380) + var(--padding-inner) * 2);
  }
}

.post-works__thumbnail-list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: rem(10);
  margin-top: rem(20);
  @include mq('md') {
    gap: rem(20);
    margin-top: rem(45);
  }
}

.post-works__thumbnail-item {
  cursor: pointer;
  transition: opacity var(--duration-base) ease;

  &:hover {
    opacity: 0.7;
  }

  &.is-active {
    opacity: 1;
  }
}

.post-works__thumbnail-img {
  height: auto;
  object-fit: cover;
  aspect-ratio: 334 / 223;
}

.post-works__navigation {
  margin-top: rem(40);
  display: flex;
  justify-content: center;
  align-items: center;
  gap: rem(50);
  @include mq('md') {
    margin-top: rem(80);
    gap: rem(80);
  }
}

.post-works__content {
  margin-top: rem(40);
  @include mq('md') {
    margin-top: rem(100);
  padding-inline: rem(100);
  }

  h2 {
    font-size: rem(26);
    font-weight: var(--font-weight-bold);
    margin-top: rem(15);
    @include mq('md') {
      font-size: rem(32);
    }
  }

  h3 {
    font-size: rem(24);
    font-weight: var(--font-weight-bold);
    margin-top: rem(15);
    @include mq('md') {
      font-size: rem(30);
    }
  }

  h4 {
    font-size: rem(22);
    font-weight: var(--font-weight-bold);
    margin-top: rem(15);
    @include mq('md') {
      font-size: rem(28);
    }
  }

  h5 {
    font-size: rem(20);
    font-weight: var(--font-weight-bold);
    margin-top: rem(15);
    @include mq('md') {
      font-size: rem(26);
    }
  }

  p {
    margin-top: rem(15);
    font-size: rem(16);
    font-weight: var(--font-weight-medium);
    @include mq('md') {
      font-size: rem(20);
    }
  }

  a {
    color: #0000ee;
    text-decoration: underline currentColor;
    font-size: rem(16);
    will-change: opacity;
    font-weight: var(--font-weight-medium);
    @include mq('md') {
      font-size: rem(20);
    }
  }

  a:hover {
    text-decoration: none;
  }

  ul {
    margin-top: rem(15);
  }

  ul li {
    padding-left: rem(20);
    font-size: rem(16);
    line-height: 1.5;
    text-indent: rem(-14);
    font-weight: var(--font-weight-medium);
    @include mq('md') {
      font-size: rem(20);
    }
  }

  ul li:not(:first-of-type),
  ol li:not(:first-of-type) {
    margin-top: rem(5);
  }

  ul li::before {
    content: "";
    margin-right: rem(3);
    position: relative;
    top: rem(-3);
    width: rem(5);
    height: rem(5);
    display: inline-block;
    border-radius: 50%;
    background-color: #000;
    margin-right: rem(7);
    @include mq('md') {
      top: rem(-4.5);
      width: rem(7);
      height: rem(7);
    }
  }

  ol {
    margin-top: rem(15);
    counter-reset: number;
    font-weight: var(--font-weight-medium);
  }

  ol li {
    padding-left: rem(20);
    font-size: rem(16);
    line-height: 1.5;
    text-indent: rem(-8);
    @include mq('md') {
      font-size: rem(20);
      text-indent: rem(-10);
    }
  }

  ol li::before {
    counter-increment: number;
    content: counter(number)".";
    display: inline-block;
    line-height: 1;
    color: #000;
    margin-right: rem(3);
  }

  figure {
    margin-top: rem(15);

    @include mq(md) {
      margin-top: rem(25);
    }
  }
}